@charset "utf-8";

.social-utilBox .txt {
    color: skin-color("green", "font3");
}

.social-wiki-club {
    .table_normal {
        .wiki-cnt {
            em {
                color: skin-color("green", "font1");
            }
        }

        .club-status {
            color: skin-color("green", "font1");
        }
    }
}

.viewBox {
    .category {
        .line:after {
            background: skin-color("green", "bgColor2");
            opacity: .2;
        }
    }

    .cnt_area {
        .is-active {
            background-color: skin-color("green", "bgColor2");
        }
    }

    &.social {
        .util_area .cnt {
            color: skin-color("green", "font1");
        }
    }
}

.social-wiki-clubview {
    .club-intro {
        .club-intro-info {
            .cnt {
                color: skin-color("green", "font1");
            }
        }
    }
}